@import '../variables';

.search {
  position: relative;


  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 5px;
    margin: auto 0;
    width: 25px;
    height: 25px;
    background: url('/assets/img/search.png') no-repeat;
    background-size: cover;
  }

  &_disabled {
    opacity: 50%;
  }
  
  &__input {
    padding-left: 40px;
    font-size: 18px;
    width: 300px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: rgb(105, 105, 105);

    &_disabled {
      &:disabled { 
        background: rgb(124, 124, 124); 
      }
    }
    
    &::-webkit-input-placeholder {
      color: rgb(196, 196, 196);
    }
    
    &:focus {
      background-color: rgba(179, 241, 253, 0.688);
      transition: 0.2s;
      
      &::-webkit-input-placeholder {
        color: transparent;
        opacity: 1; 
        transition: opacity 0.3s ease;
      }
    }
  }
  
}